import { css } from '@emotion/react';
import {
    QueryClient,
    QueryClientProvider,
} from 'react-query'

import {
    BrowserRouter,
    Routes,
    Route,
    Outlet
} from "react-router-dom";
import { Suspense } from 'react';
const queryClient = new QueryClient()
import LoginPage from './pages/login';
import DashBoard from './pages';
import routes from './routes';
import { Spin } from 'antd';

const App = () => {
    return (
        <QueryClientProvider client={queryClient}>
            <Suspense fallback={<Spin/>}>
                <BrowserRouter>
                    <Routes>
                        <Route path="/admin/login" element={<LoginPage />} />
                        <Route path="/admin" element={<DashBoard><Outlet/></DashBoard>}>
                            {
                                routes.map(({ path, component: Comp, childs }) => <Route
                                    path={path}
                                    key={path}
                                >
                                    {
                                        (childs || []).map(({ path: childPath, component: ChildComp }) => <Route
                                            path={childPath}
                                            key={childPath}
                                            element={<ChildComp />}
                                        />)
                                    }
                                    <Route index element={<Comp />} />
                                </Route>
                                )}
                            <Route index />
                        </Route>
                    </Routes>
                </BrowserRouter>
            </Suspense>
        </QueryClientProvider >
    );
};

export default App;
