import { lazy } from "react";
import {
    DatabaseFilled,
} from '@ant-design/icons';
const CampaignPage = lazy(() => import("./pages/campaign"));
const CreateCampaignPage = lazy(() => import("./pages/campaign/new"));
const EditCampaignPage = lazy(() => import("./pages/campaign/edit"));
const LeadPage = lazy(() => import("./pages/lead"));

const routes = [
    {
        path: "campaign",
        title: "Chin dch",
        component: CampaignPage,
        icon: <DatabaseFilled />,
        childs: [{
            path: "new",
            title: "Thm Chin dch",
            component: CreateCampaignPage,
        },
        {
            path: ":id",
            title: "Thm Chin dch",
            component: EditCampaignPage,
        },

        ]
    },
    {
        path: "lead",
        title: "Lead",
        component: LeadPage,
        icon: <DatabaseFilled />,
    },
];

export default routes;
