import { Layout, Menu, Breadcrumb } from 'antd';
const { Header, Content, Footer, Sider } = Layout;
const { SubMenu } = Menu;
import routes from '../routes';
import { useNavigate } from 'react-router';
import { Link } from 'react-router-dom';
import React,{ useEffect, useState } from 'react';

const DashBoard = ({ children }) => {
    const [collapsed, setCollapsed] = useState(false);
    const navigate = useNavigate();
    useEffect(() => {
        const token = localStorage.getItem("access_token");
        if (!token) {
            navigate("/admin/login");
        }
    }, []);
    return (
        <Layout style={{ minHeight: '100vh' }}>
            <Sider collapsible collapsed={collapsed} onCollapse={state => setCollapsed(state)}>
                <div className="logo" >
                    <img src={
                        collapsed ? "/images/logo-mini.png" : "/images/logo.png"
                    } />
                </div>
                <Menu theme="dark" defaultSelectedKeys={['1']} mode="inline">
                    {
                        routes.map(r => <Menu.Item key={r.path} icon={r.icon}>
                            <Link to={r.path}>{r.title}</Link>
                        </Menu.Item>)
                    }
                </Menu>
            </Sider>
            <Layout className="site-layout">
                <Header className="site-layout-background" style={{ padding: 0 }} />
                <Content style={{ margin: '0 16px' }}>
                    {children}
                </Content>
                <Footer style={{ textAlign: 'center' }}>ELCenter 2022 Created by Blacker</Footer>
            </Layout>
        </Layout >
    );
}
export default DashBoard;
